<template>
    <div>
        <div class="box">
            <table>
                <thead>
                <tr>
                    <th>商品</th>
                    <th>单价</th>
                    <th>数量</th>
                    <th>小计</th>
                </tr>
                </thead>
                <tbody>

                <tr v-for="(item1,index) in items1">
                    <td>
                        <img v-bind:src="item1.url" :title="item1.name">
                        <span>{{item1.name}}</span>
                    </td>
                    <td>{{item1.price}}</td>
                    <td>
                        <div class="smallBox">
                            <a href="" v-on:click.prevent="reduce(index)" class="ab">-</a>
                            <input type="text" v-model="item1.count" class="inp">
                            <a href="" v-on:click.prevent="add(index)" class="ab">+</a>
                            <div class="hide"></div>
                        </div>
                    </td>
                    <td>
                        <span>{{item1.count*item1.price}}</span>
                    </td>
                </tr>


                <tr v-for="(item2,index2) in items2">
                    <td>
                        <img v-bind:src="item2.url" :title="item2.name">
                        <span>{{item2.name}}</span>
                    </td>
                    <td>{{item2.price}}</td>
                    <td>
                        <div class="smallBox">
                            <a href="" v-on:click.prevent="reduce2(index2)" class="ab">-</a>
                            <input type="text" v-model="item2.count" class="inp">
                            <a href="" v-on:click.prevent="add2(index2)" class="ab">+</a>
                            <div class="hide"></div>
                        </div>
                    </td>
                    <td>
                        <span>{{item2.count*item2.price}}</span>
                    </td>
                </tr>


                <tr v-for="(item3,index2) in items3">
                    <td>
                        <img v-bind:src="item3.url" :title="item3.name">
                        <span>{{item3.name}}</span>
                    </td>
                    <td>{{item3.price}}</td>
                    <td>
                        <div class="smallBox">
                            <a href="" v-on:click.prevent="reduce3(index2)" class="ab">-</a>
                            <input type="text" v-model="item3.count" class="inp">
                            <a href="" v-on:click.prevent="add3(index2)" class="ab">+</a>
                            <div class="hide"></div>
                        </div>
                    </td>
                    <td>
                        <span>{{item3.count*item3.price}}</span>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
        <h2>总价:{{prices}}</h2>
    </div>
</template>

<script>
    import url1 from '../assets/1.jpg'
    import url2 from '../assets/2.jpg'
    import url3 from '../assets/3.jpg'
    import url4 from '../assets/4.jpg'
    import url5 from '../assets/5.jpg'
    import url6 from '../assets/6.jpg'

    export default {
        name: "shopping",
        data() {
            return {

                items1: [
                    {
                        name: '打火机',
                        count: 5,
                        price: 98,
                        url: url6
                    },
                    {
                        name: '相机',
                        count: 2,
                        price: 4388,
                        url: url5
                    }
                ],
                items2: [
                    {
                        name: 'c程序设计语言',
                        count: 5,
                        price: 32,
                        url: url3
                    },
                    {
                        name: '裤子',
                        count: 1,
                        price: 142,
                        url: url2
                    }
                ],
                items3: [
                    {
                        name: '五谷杂粮',
                        count: 5,
                        price: 99,
                        url: url1
                    },
                    {
                        name: '学习javascript',
                        count: 1,
                        price: 45,
                        url: url4
                    }
                ]
            }
        },
        methods: {
            reduce(index) {
                if (this.items1[index].count > 0) {
                    this.items1[index].count--
                }

            },
            add(index) {

                this.items1[index].count++
            },

            reduce2(index2) {
                if (this.items2[index2].count > 0) {
                    this.items2[index2].count--
                }

            },
            add2(index2) {

                this.items2[index2].count++
            },

            reduce3(index2) {
                if (this.items3[index2].count > 0) {
                    this.items3[index2].count--
                }

            },
            add3(index2) {

                this.items3[index2].count++
            },

        },
        //计算属性---计算总价
        computed: {
            prices() {
                var prices = 0
                for(let i of this.items1){
                    prices +=i.count*i.price
                }
                for(let i of this.items2){
                    prices +=i.count*i.price
                }
                for(let i of this.items3){
                    prices +=i.count*i.price
                }
                return prices
            }
        }
    }
</script>

<style scoped>
    * {
        margin: 0;
        padding: 0;
    }

    .box {
        text-align: center;
    }

    .inp {
        width: 30px;
        height: 100%;
        display: inline-block;
        float: left;
        border: none;
        padding: 2px;
        border-left: 1px solid #ccc;
        border-right: 1px solid #ccc;
    }

    .ab {
        display: inline-block;

        width: 20px;
        height: 100%;
        float: left;
    }

    .smallBox {
        border: 1px solid #ccc;
    }

    .hide {
        clear: both;
    }
</style>


