<template>
    <div>
        <a v-bind:href="url">百度</a>
        <img v-bind:src="imgUrl" alt="">
        <input type="button" v-on:click="hide" value="隐藏/显示">
        <h2 v-if="show">{{msg}}</h2>

    </div>
</template>

<script>
    import img from '../assets/logo.png'
    export default {
        name: "router4",
        data() {
            return {
                url: 'http://www.baidu.com',
                imgUrl: img,
                msg: '这是h2标签',
                show: true
            }
        },
        methods: {
            hide: function () {

                this.show = !this.show
            }
        }
    }
</script>

<style scoped>

</style>


